<template>
    <div v-if="py == 'list'" :style="{padding: '0 16px 16px'}">
        <Input v-model="searchArea.studentName" placeholder="学生姓名" style="width: 200px; padding-right: 20px"/>
        <Input v-model="searchArea.studentPhone" placeholder="学生手机号" style="width: 200px; padding-right: 20px"/>
        <Input v-model="searchArea.parentName" placeholder="家长姓名" style="width: 200px; padding-right: 20px"/>
        <Input v-model="searchArea.parentPhone" placeholder="家长手机号" style="width: 200px; padding-right: 20px"/>
        <Button type="primary" icon="ios-search" @click="search">Search</Button>
        <div :style="{padding: '16px 0 0 0',width:'90px', align: 'right'}">
            <Button type="success" icon="md-add" @click="add">新建</Button>
        </div>
        <div :style="{margin: '5px 0'}"></div>

        <Table :row-class-name="rowClassName" border :columns="columns" :data="data1">
            <template slot-scope="{ row, index }" slot="action">
                <Button type="primary" size="small" v-if="row.status ===0" style="margin-right: 5px" @click="show(row)">修改</Button>
                <Button type="error" size="small" @click="delModalShow(row)">删除</Button>
            </template>
        </Table>
        <Page :total="totalCount" :page-size="searchArea.pageSize" show-total show-sizer
              :page-size-opts="[10,20,30,40]" @on-change="changeNo" @on-page-size-change="sizeChange"/>
        <Modal v-model="delModal" width="360">
            <p slot="header" style="color:#f60;text-align:center">
                <Icon type="ios-information-circle"></Icon>
                <span>删除确认</span>
            </p>
            <div style="text-align:center">
                <p>将要删除“{{item.itemName}}”，</p>
                <p>确认吗?</p>
            </div>
            <div slot="footer">
                <Button type="error" size="large" long  @click="delIt">Delete</Button>
            </div>
        </Modal>
    </div>
    <itemsView v-else-if="py == 'add' || py == 'update' " :studentId="item.studentId" :title="py" :type="py=='add'?'生源添加':'生源修改'" @childFn="parentFn"></itemsView>
</template>

<style>
    .ivu-table .table-del-row td{
        background-color: #c3cbd6;
        color: #fff;
    }
</style>

<script>
    import _apiitems from '@/api/items'
    import expandRow from './students-list-expand';
    import itemsView from './students-view';

    export default {
        components: {
            expandRow,
            itemsView
        },
        props: {
            pageNo: {
                type: Number,
                default: 1
            },
            pageSize: {
                type: Number,
                default: 15
            },
        },

        data() {
            return {
                py: 'list', //子组件内的跳转替换值标识
                item: {},
                columns: [
                    {
                        type: 'expand',
                        width: 50,
                        render: (h, params) => {
                            return h(expandRow, {
                                props: {
                                    row: params.row
                                }
                            })
                        }
                    },
                    {
                        title: '学生姓名',
                        key: 'studentName'
                    },
                    {
                        title: '学生手机号',
                        key: 'studentPhone'
                    },
                    {
                        title: '家长姓名',
                        key: 'parentName'
                    },
                    {
                        title: '家长手机号',
                        key: 'parentPhone'
                    },
                    {
                        title: '课时费',
                        key: 'fee'
                    },
                    {
                        title: '状态',
                        key: 'status',
                        render: (h, params) => {
                            return h("div", {},  params.row.status === 0 ?"正常":"退出"  );
                        },
                        className:'demo-table-info-column'
                    },
                    {
                        title: '操作',
                        slot: 'action',
                    }
                ],
                data1: [],
                totalCount: 0,
                searchArea: {
                    studentName: '',
                    studentPhone: '',
                    parentName: '',
                    parentPhone: '',
                    pageNo: this.pageNo,
                    pageSize: this.pageSize
                },
                delModal: false,

            }
        },
        methods: {
            handleSelectAll(status) {
                this.$refs.selection.selectAll(status);
            },
            search() {
                let that = this
                _apiitems.getStudentList(that.$data.searchArea).then( res => {
                    that.$data.data1 = res.data.result
                    that.$data.totalCount = res.data.totalCount
                });
            },
            changeNo(num) {
                this.$data.searchArea.pageNo = num
                this.search();
            },
            sizeChange(size) {
                this.$data.searchArea.pageSize = size;
                this.search();
            },
            add() {
                this.$data.py = 'add';
            },
            show(it) {
                this.$data.item = it;
                this.$data.py = 'update';
            },
            delModalShow(it){
                this.$data.delModal = true;
                this.$data.item = it;
            },
            delIt(){
                _apiitems.deleteStudent(this.$data.item.studentId).then(res=>{
                    this.delModal = false;
                    this.$Message.success('删除成功！');
                    this.$emit('childFn', '1-1');
                })
            },
            parentFn(payload) {//子页面返回入库
                this.py = payload;
                this.search()
            },
            rowClassName(row, index) {
                if(row.status === 1){
                    return "table-del-row";
                }
            }
        },
        created() {
            this.search()
        }
    }
</script>
